export default function debounce(fn, immediate = false) {
    //
    let timer = null;
    //
    let isInvoke = false;
    //
    return function (...args) {
        return new Promise((resolve, reject) => {
            if (!isInvoke && immediate) {
                isInvoke = true;
                resolve(fn(...args));
            }
            //
            clearTimeout(timer);
            timer = setTimeout(() => {
                resolve(fn(...args));
            }, 200);
        });
    };
}
